.admin-portal {
    padding: 1rem;
}

.admin-portal table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 2rem;
}

.admin-portal th,
.admin-portal td {
    border: 1px solid #ddd;
    padding: 0.5rem;
    text-align: left;
}

.admin-portal th {
    background-color: #f0f0f0;
}