.app-layout {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #FFF5D6 url("../../public/assets/pattern.png") center/cover repeat;
}

.app-container {
    width: 100%;
    max-width: 20rem;
    position: relative;
    font-family: 'Dancing Script', serif;
}

.logo-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.login-title {
    display: flex;
    flex-direction: column-reverse;
    font-size: 1.3rem;
    color: #3D2E00;
    text-align: left;
    font-family: inherit;
    line-height: 1.2;
    opacity: 0;
    transform: translateX(100%);
}

.logo {
    width: 8rem;
    transform: translateX(-50%);
}